body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.shareSheetContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:wrap;position:fixed;height:188px;width:42px;z-index:1}.shareButton{margin:5px}.copyIconDiv{display:flex;justify-content:center;align-items:center;background:#7F7F7F;color:white;border-radius:50%;height:100%;width:100%}.copyLink{height:31px;width:31px;margin:5px}

.placeholderContainer{display:flex;align-items:center;flex-direction:column;height:100%;width:100%}.p{text-decoration:none}.title{font-weight:400;font-size:90px;margin-top:144px;margin-bottom:32px}.instructions{font-family:"Montserrat", sans-serif;font-size:28px}.placeholder{max-height:20vh;max-width:20vw}@media only screen and (max-width: 600px){.title{font-weight:400;font-size:50px;margin-top:100px;margin-bottom:32px}.instructions{font-size:18px}.placeholder{max-height:35vh;max-width:35vw}}.copyConfirmationContainer{position:fixed;width:100%;display:flex;justify-content:center;top:26vh;z-index:1}.copyConfirmation{display:flex;justify-content:center;align-items:center;box-shadow:3px 19px 46px;border-radius:16px;height:25px;width:70px;padding:5px;top:100px;background:white;box-shadow:4px 4px 6px gray;font-family:"Montserrat", sans-serif}#homeContainer{height:92vh;width:calc(100vw - (100vw - 100%))}.editorDiv{display:flex;justify-content:center}hr{width:50%;max-width:300px;margin-top:14px;height:1px;background-color:#000;border:none}

.logoContainer{display:flex;justify-content:flex-start}.logo{max-width:50vw;max-height:20vh}.aboutHeader{font-weight:400;font-size:90px}.aboutContainer{height:calc(100vh - (100vh - 100%));width:calc(100vw - (100vw - 100%));display:flex;justify-content:center;font-weight:400;font-size:22px;font-family:"Montserrat", sans-serif;overflow:hidden}.aboutContentContainer{display:flex}.aboutContent{width:100%;height:100%;display:flex;flex-direction:column;margin-top:5vh;padding:0 64px 0 64px}.aboutParagraph,.authors{font-size:28px;margin:16px 0 0 0}.aboutParagraph:nth-child(2),.authors:nth-child(1){margin:32px 0 0 0}.authors{font-size:18px}a:link{text-decoration:none}@media only screen and (max-width: 600px){.aboutHeader{font-size:50px}.logo{max-height:35vh;max-width:35vw}.aboutParagraph,.authors{font-size:18px}}

body{overflow:auto;font-family:"Montserrat", sans-serif}.editorContainer{display:flex;justify-content:center;position:absolute;top:0}textarea{font-size:large;font-family:'Montserrat';resize:none;margin-top:40px;border:none;overflow:auto;outline:none;box-shadow:none;resize:none;height:95vh;width:65vw;padding:0 16vw 0}@media only screen and (max-width: 600px){textarea{width:78vw;padding:0 30px 0}}@media screen and (min-device-width: 600px) and (max-device-width: 1024px){textarea{width:60vw;padding:0 165px 0}}

@media only screen and (max-width: 600px){.placeholder{top:5vh}}.copyConfirmationContainer{position:fixed;width:100%;display:flex;justify-content:center;top:26vh;z-index:1}.copyConfirmation{display:flex;justify-content:center;align-items:center;box-shadow:3px 19px 46px;border-radius:16px;height:25px;width:70px;padding:5px;top:100px;background:white;box-shadow:4px 4px 6px gray;font-family:"Montserrat", sans-serif}.editorDiv{display:flex;justify-content:center}


